import PasswordTextbox from 'kolibri.coreVue.components.PasswordTextbox';
import commonCoreStrings from 'kolibri.coreVue.mixins.commonCoreStrings';

export default {
  name: 'ResetUserPasswordModal',
  components: {
    PasswordTextbox,
  },
  mixins: [commonCoreStrings],
  props: {
    id: {
      type: String,
      required: true,
    },
    username: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      password: '',
      passwordValid: false,
      formSubmitted: false,
      busy: false,
    };
  },
  methods: {
    submitForm() {
      this.formSubmitted = true;

      if (!this.passwordValid) {
        return this.focusOnInvalidField();
      }

      this.busy = true;
      this.$store
        .dispatch('userManagement/updateFacilityUserPassword', {
          userId: this.id,
          password: this.password,
        })
        .then(() => {
          this.busy = false;
          this.$emit('cancel');
          this.showSnackbarNotification('passwordReset');
        })
        .catch(error => this.$store.dispatch('handleApiError', error));
    },
    focusOnInvalidField() {
      this.$nextTick().then(() => {
        if (!this.passwordValid) {
          this.$refs.passwordTextbox.focus();
        }
      });
    },
  },
  $trs: {
    resetPassword: 'Reset user password',
    username: 'Username: ',
  },
};

